
export default {
  cartData(context,payload){
    let cartitem=  context.state.cartList.find(item=>item.iid === payload.iid)
     if (cartitem) {
      context.commit('cartCount',cartitem)
     } else {
      payload.count=1;
      payload.selected=false;
      context.commit('cartAddnew',payload)
     } 
      // this.state.cartList.push(payload)
    }
}